@mixin render($obj) {
    @for $i from 1 through 200 {
        .p#{$i} {
            transition-delay: random(500) + ms;
            background: rgba(#FFF,random(8)/10);
            @if length($obj) >= $i {
                clip-path: nth($obj, $i);
            } @else {
                clip-path: $void;
            }
        }
    }
}



.is-css {
    @include render($css);
}

.is-js {
    @include render($js);
}

.is-html {
    @include render($html);
}

.canvas {
    position: relative;
    width: 800px;
    height: 600px;
    background-color: #000;
    margin: 0 auto;
    &>*{
        position: absolute;
        width: 800px;
        height: 600px;
        left: 0;
        top: 0;
        transition: all .8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    }
}


