.canvas {
  position: relative;
  width: 800px;
  height: 600px;
  background-color: #000;
  margin: 0 auto;
}

.canvas > * {
  position: absolute;
  width: 800px;
  height: 600px;
  left: 0;
  top: 0;
  transition: all 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.block {
  display: flex;
}
